import React from 'react';
import STEP_1 from './images/home-step-1.png';
import STEP_2 from './images/home-step-2.png';
import STEP_3 from './images/home-step-3.png';

import LOGO_HORIZONTAL from './images/logo-horizontal.jpeg';
import LOGO_HORIZONTAL_WHITE from './images/logo-horizontal-white.png';
import LOGO_VERTICAL from './images/logo-vertical.jpeg';
import SLIDE_1 from './images/slide-1.jpg';
import SLIDE_1_MOBILE from './images/slide-1-mobile.jpg';
import STARS_BACKGROUND from './images/stars-background.jpg';

import IMG_LANDING  from './images/img-rechazo.jpg';
import IMG_UPLOAD_DOC  from './images/pop-picture-upload-doc.jpg';
import IMG_FALTA_POCO  from './images/pop-picture-falta-poco.jpg';

export const FirstStepImg = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={STEP_1} alt='Selecciona un producto financiero' {...props} />;
}

export const SecondStepImg = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={STEP_2} alt='Compara tus opciones de crédito' {...props} />;
}

export const ThirdStepImg = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={STEP_3} alt='Solicita tu crédito desde tu hogar' {...props} />;
}



export const LogoHorizontalImg = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={LOGO_HORIZONTAL} alt='Finanservs credito prestamo' {...props} />;
}

export const LogoHorizontalWhiteImg = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={LOGO_HORIZONTAL_WHITE} alt='Finanservs crédito préstamo' {...props} />;
}

export const FinanservesVerticalLogoImg = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={LOGO_VERTICAL} alt='Finanservs logo vertical' {...props} />;
}


export const Rechazo = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={IMG_LANDING} alt='Selecciona un producto financiero' {...props} />;
}
export const CargaDocumento = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={IMG_UPLOAD_DOC} alt='Selecciona un producto financiero' {...props} />;
}
export const FaltaPoco = (props: React.ImgHTMLAttributes<HTMLImageElement>) => {
  return <img src={IMG_FALTA_POCO} alt='Selecciona un producto financiero' {...props} />;
}

export const FirstSlidePath = SLIDE_1;
export const FirstSlideMobilePath = SLIDE_1_MOBILE;
export const SecondSlidePath = SLIDE_1;
export const ThirdSlidePath = SLIDE_1;
export const StarsBackgroundPath = STARS_BACKGROUND;
