import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import {
  STEPS,
  Application
} from "../types";
import Forms from '../Forms';


interface Stepper {
  next?: () => void;
  prev?: () => void;
  disabled: boolean;
  Form: React.ReactElement;
  step: STEPS
};

